$(document).ready(function () {
    $('div.delete').on('click', function() {
        $(this).parent().slideUp();
    })
    $('div.minus').click(function () {
        var $input = $(this).parent().find('input');
        var count = parseInt($input.val()) - 1;
        count = count < 1 ? 1 : count;
        $input.val(count);
        $input.change();
        if($input.val() < 9) {
            $input.css({
                width: '10px'
            });
        }
        return false;
    });
    $('.plus').click(function () {
        var $input = $(this).parent().find('input');
        $input.val(parseInt($input.val()) + 1);
        $input.change();
        if($input.val() > 9) {
            $input.css({
                width: '15px'
            });
        }
        return false;
    });
});